    padding: 25px 100px;
}

.menu-heading {
    font-size: 4.2rem;
    font-weight: 800;
    letter-spacing: 2px;
}

.menu-subheading-container {
    display: flex;
    justify-content: space-between;
}

.view-more-btn {
    text-transform: uppercase;
    font-size: 1.6rem;
    font-weight: 600;
    background-color: #FF4D4D;
    color: #FFE6E6;
    border: 3px solid #FF4D4D;
    padding: 10px 15px;
    border-radius: 5px;
}

.view-more-btn:hover {
    background-color: #FFE6E6;
    color: #FF4D4D;
    transition: all 200ms ease-in;
}

.menu-subheading {
    font-size: 2.1rem;
    font-weight: 600;
    color: #262626;
    display: flex;
    align-items: center;
}

.menu-items {
    display: flex;
    flex-wrap: wrap;
    gap: 40px;
    padding-top: 40px;
}

.menu-item {
    width: calc(33.33% - 40px);
    background-color: #FF4D4D;
    padding: 30px;
    border-radius: 10px;
}

.menu-item .img-container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.menu-item:nth-child(1) img, .menu-item:nth-child(2) img, .menu-item:nth-child(3) img {
    height: 180px;
}

.menu-item:nth-child(4) img, .menu-item:nth-child(5) img, .menu-item:nth-child(6) img {
    height: 240px;
}

.menu-item h2 {
    font-size: 2.4rem;
    font-weight: 800;
    color: #FFE6E6;
    margin-top: 40px;
    margin-bottom: 20px;
}

.price-details {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.menu-item p {
    font-size: 1.8rem;
    font-weight: 600;
    color: #FFE6E6;
}

.menu-item button {
    text-transform: uppercase;
    font-size: 1.6rem;
    font-weight: 600;
    background-color: #FF4D4D;
    color: #FFE6E6;
    border: 3px solid #FFE6E6;
    padding: 10px 15px;
    border-radius: 5px;
}

.menu-item button:hover {
    background-color: #FFE6E6;
    color: #FF4D4D;
    transition: all 200ms ease-in;
}

.img-credit {
    padding: 0;
    margin-top: 5px;
}`, "",{"version":3,"sources":["webpack://./src/menu/index.css"],"names":[],"mappings":"AAAA;IACI,mBAAmB;AACvB;;AAEA;IACI,iBAAiB;IACjB,gBAAgB;IAChB,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,8BAA8B;AAClC;;AAEA;IACI,yBAAyB;IACzB,iBAAiB;IACjB,gBAAgB;IAChB,yBAAyB;IACzB,cAAc;IACd,yBAAyB;IACzB,kBAAkB;IAClB,kBAAkB;AACtB;;AAEA;IACI,yBAAyB;IACzB,cAAc;IACd,6BAA6B;AACjC;;AAEA;IACI,iBAAiB;IACjB,gBAAgB;IAChB,cAAc;IACd,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,eAAe;IACf,SAAS;IACT,iBAAiB;AACrB;;AAEA;IACI,0BAA0B;IAC1B,yBAAyB;IACzB,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,iBAAiB;IACjB,gBAAgB;IAChB,cAAc;IACd,gBAAgB;IAChB,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,8BAA8B;AAClC;;AAEA;IACI,iBAAiB;IACjB,gBAAgB;IAChB,cAAc;AAClB;;AAEA;IACI,yBAAyB;IACzB,iBAAiB;IACjB,gBAAgB;IAChB,yBAAyB;IACzB,cAAc;IACd,yBAAyB;IACzB,kBAAkB;IAClB,kBAAkB;AACtB;;AAEA;IACI,yBAAyB;IACzB,cAAc;IACd,6BAA6B;AACjC;;AAEA;IACI,UAAU;IACV,eAAe;AACnB","sourcesContent":[".menu {\n    padding: 25px 100px;\n}\n\n.menu-heading {\n    font-size: 4.2rem;\n    font-weight: 800;\n    letter-spacing: 2px;\n}\n\n.menu-subheading-container {\n    display: flex;\n    justify-content: space-between;\n}\n\n.view-more-btn {\n    text-transform: uppercase;\n    font-size: 1.6rem;\n    font-weight: 600;\n    background-color: #FF4D4D;\n    color: #FFE6E6;\n    border: 3px solid #FF4D4D;\n    padding: 10px 15px;\n    border-radius: 5px;\n}\n\n.view-more-btn:hover {\n    background-color: #FFE6E6;\n    color: #FF4D4D;\n    transition: all 200ms ease-in;\n}\n\n.menu-subheading {\n    font-size: 2.1rem;\n    font-weight: 600;\n    color: #262626;\n    display: flex;\n    align-items: center;\n}\n\n.menu-items {\n    display: flex;\n    flex-wrap: wrap;\n    gap: 40px;\n    padding-top: 40px;\n}\n\n.menu-item {\n    width: calc(33.33% - 40px);\n    background-color: #FF4D4D;\n    padding: 30px;\n    border-radius: 10px;\n}\n\n.menu-item .img-container {\n    displa